* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

/*____________________*/
::-webkit-scrollbar-track {
	background-color: #fff;
	border: 1px solid black;
}

::-webkit-scrollbar {
	width: 13px;
	background-color: black;
}

::-webkit-scrollbar-thumb {
	background-color: rgb(0, 0, 0);
	border: 1px solid black;
	background-image: -webkit-linear-gradient(90deg,
			rgb(25, 118, 210) 0%,
			rgba(25, 118, 210) 25%,
			transparent 100%,
			rgba(0, 0, 0, 1) 75%,
			transparent)
}